.World {
  border: 1px solid #000;
  background-color: #fff;
  overflow: hidden;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 200;
  -webkit-transition:
    height 0.3s ease-in-out 0.1s,
    left 0.2s ease-in,
    top 0.2s ease-in,
    width 0.2s ease-in;
  transition:
    height 0.3s ease-in-out 0.1s,
    left 0.2s ease-in,
    top 0.2s ease-in,
    width 0.2s ease-in;
}

.World.expanded {
  height: calc(100% - 32px);
  top: 16px;
  right: 16px;
  bottom: 16px;
  left: 16px;
  -webkit-transition:
    height 0.3s ease-in-out,
    left 0.3s ease-in,
    top 0.3s ease-in,
    width 0.3s ease-out;
  transition:
    height 0.3s ease-in-out,
    left 0.3s ease-in,
    top 0.3s ease-in,
    width 0.3s ease-out;
  /* transition:
     background-position 0.2s ease-out,
     width 0.1s ease-in,
     height 0.2s ease-in-out 0.2s,
     left 0.2s ease-in; */
}

.World:not(.expanded) {
  cursor: pointer;
}

.clickableArea {
  position: absolute;
  width: 100%;
  height: 100%;
  cursor: pointer;
  /*   z-index: 300; */
}

.World.expanded .clickableArea {
  background-size: 256px;
  background-position: calc(50% - 10px) 124px;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  opacity: 0;
  -webkit-animation: tunnel-appear 8 24s step-end;
          animation: tunnel-appear 8 24s step-end;
}

@-webkit-keyframes tunnel-appear {
  from { opacity: 0 }
  10% { opacity: 0 }
  11% { opacity: 0.2 }
  13% { opacity: 0 }
  50% { opacity: 0 }
  51% { opacity: 0.2 }
  65% { opacity: 0 }
  80% { opacity: 0 }
  81% { opacity: 0.2 }
  85% { opacity: 0 }
  to { opacity: 0 }
}

@keyframes tunnel-appear {
  from { opacity: 0 }
  10% { opacity: 0 }
  11% { opacity: 0.2 }
  13% { opacity: 0 }
  50% { opacity: 0 }
  51% { opacity: 0.2 }
  65% { opacity: 0 }
  80% { opacity: 0 }
  81% { opacity: 0.2 }
  85% { opacity: 0 }
  to { opacity: 0 }
}

.World.shortened .clickableArea {
  top: 0;
  
}

.World.collapsed {
  height: 48px;
  left: 16px;
  width: calc(100vw - 32px);
}

.World.shortened {
  width: 240px;
  height: 48px;
}

.World.shortened::before {
  content: '';
  display: block;
  position: relative;
  width: 5px;
  height: 48px;
  background-image: url(/portfolio2018/static/media/noise.a9283046.png);
  background-size: 1024px 768px;
  -webkit-animation: noise-appear 0.8s ease-in;
          animation: noise-appear 0.8s ease-in;
}

@-webkit-keyframes noise-appear {
  from{ width: 0; }
  80%{ width: 0; }
  to{ width: 5px; }
}

@keyframes noise-appear {
  from{ width: 0; }
  80%{ width: 0; }
  to{ width: 5px; }
}

