.ProjectList::before {
  content: "";
  height: calc(100% - 54px);
  background-repeat: repeat-y;
  background-size: 1px;
  background-position: 8px 0;
  -webkit-animation: Marching-dots-slide-in 1 0.4s ease-in, Marching-dots 1.2s 16 ease-in-out 0.3s;
          animation: Marching-dots-slide-in 1 0.4s ease-in, Marching-dots 1.2s 16 ease-in-out 0.3s;
}

@-webkit-keyframes Marching-dots-slide-in {
  from {
    opacity: 0;
    height: 0px;
  }
  50% {
    opacity: 0;
    height: 0px;
  }
  to {
    opacity: 1;
    height: calc(100% - 54px);
  }
}

@keyframes Marching-dots-slide-in {
  from {
    opacity: 0;
    height: 0px;
  }
  50% {
    opacity: 0;
    height: 0px;
  }
  to {
    opacity: 1;
    height: calc(100% - 54px);
  }
}

@-webkit-keyframes Marching-dots {
  from {
    /* fixes flickering markup in Safari dev tools */
    content: "";
    background-position: 8px 0;
  }
  25% {
    background-position: 8px 1px;
  }
  50% {
    background-position: 8px 2px;
  }
  75% {
    background-position: 8px 3px;
  }
  to {
    content: "";
    background-position: 8px 4px;
  }
}

@keyframes Marching-dots {
  from {
    /* fixes flickering markup in Safari dev tools */
    content: "";
    background-position: 8px 0;
  }
  25% {
    background-position: 8px 1px;
  }
  50% {
    background-position: 8px 2px;
  }
  75% {
    background-position: 8px 3px;
  }
  to {
    content: "";
    background-position: 8px 4px;
  }
}

